@import '../../sass/mixin';

.postcreator {
    display: flex;
    flex-direction: column;
    margin-top: 30px;
    border-radius: 30px;
    background-color: #ffffff;
    width: 100%;
    box-shadow: 0 5px 7px -7px rgba(0, 0, 0, 0.75);

    &__top {
        display: flex;
        padding: 15px;
        border-bottom: 1px solid #eff2f5;

        form {
            flex: 1;
            display: flex;

            input {
                background-color: #eff2f5;
                outline-width: 0;
                border: none;
                padding: 5px 20px;
                margin: 0 10px;
                border-radius: 999px;
                font-size: 15px;
                cursor: pointer;
                transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;

                &:hover {
                    background-color: #e4e6e9;
                    color: #606266; } }

            button {
                display: none; } } }

    &__input {
        flex: 1;
        background-color: #eff2f5;
        outline-width: 0;
        border: none;
        padding: 5px 20px;
        margin: 0 10px;
        border-radius: 999px;
        font-size: 15px;
        cursor: pointer;
        transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;

        &:hover {
            background-color: #e4e6e9;
            color: #606266; } }

    &__bottom {
        display: flex;
        justify-content: space-evenly;

        &--option {
            display: flex;
            align-items: center;
            color: gray;
            margin: 5px;
            padding: 12px 20px;
            border-radius: 10px;
            cursor: pointer;
            transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;

            h3 {
                font-size: medium;
                margin-left: 10px; }

            &:hover {
                background-color: #eff2f5;
                color: #606266; }

            &.nomob {
                display: none;
                @include mq(600px) {
                    display: flex; } } } } }
